export default {
	data() {
		return {
			oldTel: '',
			code: '',
			codeIcon: false,
			btnData: '获取验证码',
			disabled: false,
			userTel: '',
			TelIcon: false,
			formBtn: true,
			timer:''
		}
	},
	onLoad: function(options) {
		this.oldTel = options.userTel
	},
	methods: {
		gainCode: function() {
			uni.request({
				url: this.$Url + '/api/sms?mobile=' + uni.getStorageSync('userData').phone,
				method: 'GET',
				data: {},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					if (res.statusCode == 200) {
						this.countdown(60)
						this.disabled = true
						uni.showToast({
							title: '发送成功',
							duration: 1000
						});
					} else {
						uni.showToast({
							icon: 'none',
							title: '网络不给力，请稍后重试',
							duration: 1000
						});
					}
				}
			});
		},
		tapCode: function(e) {
			this.code = e.target.value;
			if (e.target.value.length > 0) {
				this.codeIcon = true;
			} else {
				this.codeIcon = false;
			}
			if (this.code.length == 6 && this.userTel.length == 11) {
				this.formBtn = false
			}
		},
		clearCodeIcon: function() {
			this.code = '';
			this.codeIcon = false;
		},
		countdown: function(time) {
			let that = this;
			if (time == 0) {
				that.btnData = '获取验证码'
				that.disabled = false
			} else {
				time--
				that.btnData = time + 's后获取'
				that.disabled = true
				that.timer = setTimeout(function() {
					that.countdown(time)
				}, 1000)
			}
		},
		tapUser: function(e) {
			this.userTel = e.target.value;
			if (e.target.value.length > 0) {
				this.TelIcon = true;
			} else {
				this.TelIcon = false;
			}
			if (this.code.length == 6 && this.userTel.length == 11) {
				this.formBtn = false
			}
		},
		clearUserIcon: function() {
			this.userTel = '';
			this.TelIcon = false;
		},
		formSubmit: function(e) {
			if (this.code.length < 1) {
				uni.showToast({
					icon: 'none',
					title: '请输入昵称！',
					duration: 1000
				});
			} else if (this.userTel.length < 1) {
				uni.showToast({
					icon: 'none',
					title: '请输入新手机号！',
					duration: 1000
				});
			} else if (!(/^[1][3,4,5,7,8][0-9]{9}$/).test(this.userTel)) {
				uni.showToast({
					icon: 'none',
					title: '手机号格式不正确！',
					duration: 1000
				});
			} else {
				uni.request({
					url:  this.$Url+'/api/check',
					method: 'POST',
					data: {
						mobile:uni.getStorageSync('userData').phone,
						code:this.code
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						if(res.data.status_code == 200){
							uni.request({
								url: this.$Url + '/api/v1/change/user/phone', //仅为示例，并非真实接口地址。
								method: 'POST',
								data: {
									token: uni.getStorageSync('token'),
									phone: this.userTel
								},
								header: {
									'content-type': 'application/x-www-form-urlencoded'
								},
								success: (res) => {
									if (res.data.code == 200) {
										uni.showToast({
											title: res.data.msg,
											duration: 1000
										});
										this.code = ''
										this.oldTel = this.userTel
										this.userTel = ''
										this.disabled = false
										this.btnData =  '获取验证码'
										clearTimeout(this.timer)
										this.timer = ''
										
									} else {
										uni.showToast({
											icon: 'none',
											title: res.data.msg,
											duration: 1000
										});
									}
								},
							})
						
						}else{
							uni.showToast({
								icon: 'none',
								title: res.data.msg,
								duration: 1000
							});
						}
					}
				});
				
			}
		}
	}
}
